* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  width: 100%;
  height: 100%;
  background: #000;
  flex-direction: column;
  display: flex;
}

header#header-main {
  max-height: 128px;
  background-color: #555;
  flex-direction: row;
  align-items: center;
  padding: 4px 16px;
  display: flex;
}

#header-name {
  color: #f5f5f5;
  flex: 1;
  font-family: Lobster;
  font-size: larger;
}

#header-links {
  flex-wrap: wrap;
  flex: 2;
  grid-template-columns: 1fr 1fr;
  display: grid;
}

#header-links > button, #header-links > a {
  color: #f5f5f5;
  min-height: 48px;
  max-height: 48px;
  box-sizing: border-box;
  background-color: #333;
  border: none;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin: 2px;
  padding: 8px;
  display: flex;
}

#header-links > button:hover, #header-links > a:hover {
  cursor: pointer;
  background-color: #888;
}

body > article {
  height: 100%;
  flex: 1;
  display: flex;
}

#right-panel {
  color: #f5f5f5;
  min-width: 256px;
  height: 100%;
  border-left: 1px solid #fff;
  flex-direction: column;
  flex: 2;
  display: flex;
}

#right-panel > * {
  max-height: 33%;
  flex: 1;
}

#properties {
  max-height: 100%;
  height: 100%;
  flex-direction: column;
  flex: 2;
  justify-content: flex-start;
  align-items: center;
  padding-top: 8px;
  display: flex;
}

#properties > * {
  width: 100%;
  box-sizing: border-box;
  min-height: 32px;
  margin-bottom: 8px;
}

.frames {
  max-height: 50%;
  flex-direction: column;
  flex: 1;
  justify-content: space-between;
  display: flex;
}

#display {
  flex-direction: column;
  flex: 6;
  justify-content: center;
  display: flex;
}

#controls {
  justify-content: center;
  align-items: center;
  display: flex;
}

#controls > button {
  background-color: #555;
}

#canvases {
  flex: 1;
  justify-content: center;
  display: flex;
  position: relative;
  overflow-y: auto;
}

#canvases > canvas {
  width: 100%;
  height: auto;
  max-width: 100%;
  flex: 1;
  position: absolute;
}

canvas#ui {
  pointer-events: none;
  image-rendering: crisp-edges;
  image-rendering: pixelated;
}

#items {
  border-right: 1px solid #fff;
  flex-direction: column;
  flex: 1;
  display: flex;
  overflow-y: hidden;
}

#current-frame {
  border-top: 2px solid #fff;
}

#current-frame > .list {
  flex-direction: column-reverse;
}

.list {
  max-width: 100%;
  flex-direction: column;
  display: flex;
  overflow-y: auto;
}

.list .frame-preview {
  cursor: pointer;
  height: auto;
  flex-direction: column;
  margin-top: 8px;
  display: flex;
  position: relative;
}

.frame-preview img {
  width: 100%;
  height: auto;
  object-fit: contain;
}

.frame-preview header {
  color: #fff;
  text-shadow: 0 0 4px #000;
  border-radius: 4px;
  font-weight: bold;
  position: absolute;
  top: 4px;
  left: 4px;
}

.frame-preview footer {
  width: 100%;
  height: 20px;
  display: flex;
}

.frame-preview div {
  color: #fff;
  width: 100%;
  height: 28px;
  text-align: center;
  background-color: gray;
  border-radius: 8px 8px 0 0;
  overflow: hidden;
}

.frame-preview.focus {
  border-radius: 8px 8px 0 0;
  outline: 2px solid #aee;
}

.frame-preview.focus div {
  background-color: #333;
  border-radius: 8px 8px 0 0;
  outline: 2px solid #aee;
}

footer button {
  flex: 1;
}

